.milk {
  border: none;
  padding: 0;
}

.milk__title {
  @include font-title(16px, 24px, 600);

  margin: 0 0 23px 0;
  padding: 0;
  color: $first-color;

  @media (min-width: $tablet) {
    margin: 0 0 20px 0;
  }
}

.milk__list {
  @include zero(0, 0, none);
}

.milk__item {
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin: 0 0 23px 0;
  }

  @media (min-width: $tablet) {
    &:not(:last-child) {
      margin: 0 0 20px 0;
    }
  }
}

.milk__custom-radio {
  position: relative;
  cursor: pointer;
}

.milk__control-radio {
  appearance: none;
  position: absolute;

  &:checked + .milk__box-radio::before {
    display: block;
  }

  &:checked + .milk__box-radio {
    border: 2px solid #7859cf;
  }

  &:disabled + .milk__box-radio {
    border: 2px solid #e2e2e2;
  }
}

.milk__box-radio {
  position: absolute;
  top: 0;
  border: 2px solid #bdbdbd;
  border-radius: 50px;
  width: 24px;
  height: 24px;

  &::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #7859cf;
    display: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &:hover {
    border: 2px solid #9070ec;
  }

  &:hover::before {
    display: block;
    background: rgba(120, 89, 207, 0.25);
  }
}

.milk__title-radio {
  @include font-title(16px, 24px, 400);

  color: $first-color;
  margin-left: 34px;

  &--disabled {
    color: #bdbdbd;
  }
}
