*,
**::after,
**::before {
  box-sizing: border-box;
}

.button {
  box-sizing: border-box;
  background: #7859cf;
  border-radius: 4px;
  border: none;
  padding: 0;
}

html {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Montserrat-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #333333;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

