.menu-toggle {
  position: relative;
  border: none;
  background: transparent;
  width: 16px;
  height: 16px;
  padding: 22px 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.2s;
  cursor: pointer;
}
.menu-toggle:active {
  background: #f0ebff;
}
@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}
.menu-toggle--nojs {
  display: none;
}

.menu-toggle__line {
  height: 2px;
  width: 16px;
  background: #333333;
  position: absolute;
  left: 13px;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
.menu-toggle__line::after {
  content: "";
  left: 0;
  position: absolute;
  top: 4px;
  height: 2px;
  width: 16px;
  background: #333333;
  transition: transform 0.25s, top 0.25s 0.25s;
}
.menu-toggle__line::before {
  content: "";
  left: 0;
  position: absolute;
  top: -4px;
  height: 2px;
  width: 16px;
  background: #333333;
  transition: transform 0.25s, top 0.25s 0.25s;
}

.menu-toggle.open span {
  transition-duration: 0.1s;
  transition-delay: 0.25s;
  background: transparent;
}

.menu-toggle.open span::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(-45deg);
}

.menu-toggle.open span::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(45deg);
}

