.header__container {

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  margin: 0 auto;

  @media (min-width: $tablet) {
    padding: 0 20px;
    flex-wrap: nowrap;
  }

  @media (min-width: $desktop) {
    padding: 0 40px;
    max-width: 1440px;
  }
}

.header__container > a {
  display: flex;
  margin-right: auto;
  text-decoration: none;
  padding: 0 0 0 20px;

  @media (min-width: $tablet) {
    padding: 0;
    margin-right: 67px;
  }

  @media (min-width: $desktop) {
    margin-right: 162px;
  }

  &:focus {
    outline: 2px solid $active-text;
    outline-offset: 5px;
  }
}

.header__container img {
  transition: 0.2s ease-in-out;

  @media (min-width: $tablet) {
    width: 82px;
    height: 36px;
  }

  @media (min-width: $desktop) {
    width: 170px;
    margin-right: 20px;
  }
}

.header__logo > p {
  display: none;

  @media (min-width: $desktop) {
    @include font-title(12px, 16px, 400);

    margin: 0;
    width: 126px;
    display: block;
    color: $first-color;
  }
}

.header__main-nav {
  order: 3;
  width: 100%;

  @media (min-width: $tablet) {
    order: 0;
    width: auto;
  }

  @media (min-width: $desktop) {
    margin-right: 162px;
  }
}
